.album-container{
    display: flex;
    flex-flow: row wrap;
}

.album-box{
    max-width: 120px;
}

.album-cover{
    max-width: 100px;
}

.album-cover img{
    max-width: 100px;
}

.album-cover img:hover{
    filter: grayscale(100%);
    border: 2px solid rgba(0, 0, 0, 0.571);
}